@use '../base/globals' as *;

.services-block {
  @include container;
  min-height: 1100px;
  padding: 64px;
  border-radius: 24px;
  background: $white;

  &__wrapper {
    position: relative;
  }

  &__items {
    @include container;

    @include respond(small) {
      @include flex-between;
      align-items: flex-start;
    }
  }

  &__block-heading {
    @include container;
    position: relative;
    margin-bottom: 56px; 
    font-size: clamp(28px, 4vw, 1vw);
    line-height: 1;
    font-family: $font-secondary;
    text-transform: uppercase;
    color: $black;
  }

  &__services {
    margin-bottom: 16px;

    @include respond(small) {
      width: 50%;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__heading {
    margin-bottom: 24px;
    padding-bottom: 24px; 
    font-family: $font-primary;
    font-size: 20px;
    line-height: 32px;
    color: $black;
    cursor: pointer;
    transition: all .2s ease;

    @include respond-up(small){
      font-size: 24px;
    }

    /* underline system, single line on mouse out */
    position: relative;

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      height: 2px;
      pointer-events: none;
    }

    /* base line always visible */
    &::before {
      background-color: $colorTwo;
    }

    /* hover overlay that animates, never leaves a second line on mouse out */
    &::after {
      background-color: $colorOne;
      transform: scaleX(0);
      transform-origin: left center;
      transition: transform 260ms ease;
      will-change: transform;
    }

    &:hover::after,
    &:focus-visible::after,
    &.active::after {
      transform: scaleX(1);
    }

    &.active + .services-block__description {
      height: 800px;
      overflow-y: scroll;
    }
  }

  &__description {
    display: none;
    position: relative;
    height: unset;
    overflow-y: unset;
    margin-bottom: 80px;
    color: $black;

    * {
      line-height: 32px;
    }

    h1, h2, h3, h4, h5, h6 {
      margin: 48px 0 24px;
    }
  }

  /* Images column */
  &__images {
    display: none;
    position: relative;

    @include respond(small) {
      display: block;
      width: 50%;
    }

    .services-block__image {
      display: flex;
      justify-content: center;
      position: absolute;
      top: -80px;
      left: 0;
      width: 100%;
      opacity: 0;   
      visibility: hidden;
      z-index: 0;
      transition:
      opacity 0.6s ease-in,
      visibility 0.6s ease-in,
      z-index 0s ease-in 0.6s;

      img {
        display: block;
        width: auto;
        height: 1000px;
        object-fit: contain;
      }

      &.is-active {
        opacity: 1;
        visibility: visible;
        z-index: 1;
        transition-delay: 0s;
      }

      &.is-hover {
        opacity: 1;
        visibility: visible;
        z-index: 2;
        transition-delay: 0s;
      }
    }
  }

  .primary-button {
    margin: 40px 0;
  }

  #anchor {
    padding-top: 96px;
  }
}

/* Hide the active image while hovering a different heading, then restore cleanly on mouse out */
.services-block.is-hovering .services-block__image.is-active:not(.is-hover) {
  opacity: 0;
  visibility: hidden;
}

/* Progressive reveal inside description */
.services-block {
  &__description {
    // paragraphs default to visible, JS toggles classes as needed
    p {
      transition: opacity 300ms ease, transform 300ms ease;
      will-change: opacity, transform;

      &.is-hidden {
        opacity: 0;
        transform: translateY(8px);
      }

      &.is-visible {
        opacity: 1;
        transform: none;
      }
    }
  }
}
